import './App.css';
import React from 'react'
import { bubble as Menu } from 'react-burger-menu'
import DailyCases from './DailyCases';
import DCMap from './DCMap';
import ReactFC from 'react-fusioncharts';
import Doughnut from './Doughnut';
import DailyDeaths from './DailyDeaths';
import DoughnutDeaths from './DoughnutDeaths';
import HospitalAdmissions from './HospitalAdmissions';
import ICUAdmissions from './ICUAdmissions';
import CasesAge from'./CasesAage';

import FusionCharts from 'fusioncharts';
import StackedColumn2DLine from 'fusioncharts/fusioncharts.charts';
import FusionTheme from 'fusioncharts/themes/fusioncharts.theme.fusion';
ReactFC.fcRoot(FusionCharts, StackedColumn2DLine, FusionTheme);

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: true,
      cases: false,
      home: true,
      deaths: false,
      hospital: false,
      testing: false,

    }
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
    this.handleCasesClick = this.handleCasesClick.bind(this);
    this.handleDeathsClick = this.handleDeathsClick.bind(this);
    this.handleHomeClick = this.handleHomeClick.bind(this);
    this.handleHospitalClick = this.handleHospitalClick.bind(this);
    this.handleTestsClick=this.handleTestsClick.bind(this);
  
  }

  onSetSidebarOpen(open) {
    this.setState({
      sidebarOpen: open
    })
  }
  handleStateChange(state) {
    this.setState({ MenuOpen: state.isOpen })
  }

  closeMenu() {
    this.setState({ MenuOpen: false })
  }

  toggleMenu() {
    this.setState(state => ({ MenuOpen: !state.MenuOpen }))
  }
  handleCasesClick() {
    this.setState({
      home: false,
      cases: true,
      deaths: false,
      hospital: false,
      MenuOpen: false,
      testing: false,

    })
  }
  handleDeathsClick() {
    this.setState({
      home: false,
      cases: false,
      deaths: true,
      MenuOpen: false,
      hospital: false,
      testing: false,

    })
  }
  handleHomeClick() {
    this.setState({
      home: true,
      cases: false,
      deaths: false,
      MenuOpen: false,
      hospital: false,
      testing: false,

    })
  }
  handleHospitalClick() {
    this.setState({
      home: false,
      cases: false,
      deaths: false,
      MenuOpen: false,
      hospital: true,
      testing: false,

    })
  }
  handleTestsClick(){
    this.setState({
      home: false,
      cases: false,
      deaths: false,
      MenuOpen: false,
      hospital: false,
      testing: true,
    })
  }


  render() {


    const dataSource = {
      chart: {
        caption: "Daily Testing",
        subcaption: "Last 7 days",
        showsum: "1",
        plottooltext:
          "$label had <b>$dataValue</b> $seriesName tests",
        theme: "fusion",
        drawcrossline: "1"
      },
      categories: [
        {
          category: [
            {
              label: "9th Nov"
            },
            {
              label: "10th Nov"
            },
            {
              label: "11th Nov"
            },
            {
              label: "12th Nov"
            },
            {
              label: "13th Nov"
            },
            {
              label: "14th Nov"
            },
            {
              label: "15th Nov"
            }
          ]
        }
      ],
      dataset: [
        {
          seriesname: "Positive",
          data: [
            {
              value: "912"
            },
            {
              value: "832"
            },
            {
              value: "1261"
            },
            {
              value: "1212"
            },
            {
              value: "1357"
            },
            {
              value: "1118"
            },
            {
              value: "1159"
            }
          ]
        },
        {
          seriesname: "Negative",
          data: [
            {
              value: "10567"
            },
            {
              value: "9667"
            },
            {
              value: "20835"
            },
            {
              value: "21671"
            },
            {
              value:"25638"
            },
            {
              value: "21048"
            },
            {
              value: "17537"
            }
          ]
        },
       
      ]
    };
    
    const chartConfigs = {
      type: 'stackedcolumn2dline',
      renderAt: 'chart-container',
      width: '700',
      height: '390',
      dataFormat: 'json',
      dataSource:  dataSource
      

    };
    let content;
    if (this.state.home) {
      content = (
        <div>
          <h2>Home</h2>
        </div>
      );
    } else if (this.state.cases) {
      content = (
        <div>
          <h2>Cases</h2>
          <DailyCases />
          <br></br>
          <DCMap />
          <br></br>
          <Doughnut />
          <br></br>
          <CasesAge/>
        </div>
      );
    } else if (this.state.deaths) {
      content = (
        <div>
          <h2>Deaths</h2>
          <DailyDeaths />
          <br></br>
          <DoughnutDeaths />
        </div>
      );
    }
    else if (this.state.hospital) {
      content = (
        <div>
          <h2>Hospitals</h2>
          <HospitalAdmissions />
          <br></br>
          <ICUAdmissions />

        </div>
      );
    }else if(this.state.testing){
      content = (
        <div>
          <h2>Testing</h2>
          <ReactFC {...chartConfigs} />
          <br></br>

        </div>
      );

    }

    return (
      <div className="App">
        <Menu isOpen={this.state.MenuOpen} onStateChange={(state) => this.handleStateChange(state)} pageWrapId={"page-wrap"} outerContainerId={"outer-container"}>
          <span id="Home" className="menu-item" onClick={this.handleHomeClick}>Home</span>
          <span id="cases" className="menu-item" onClick={this.handleTestsClick}>Testing</span>
          <span id="cases" className="menu-item" onClick={this.handleCasesClick}>Cases</span>
          <span id="cases" className="menu-item" onClick={this.handleHospitalClick}>Hospitals</span>
          <span id="cases" className="menu-item" onClick={this.handleDeathsClick}>Deaths</span>

        </Menu>
        <main id="page-wrap">
          <h1>Covid-19</h1>
          {content}
        </main>
      </div>
    );
  }
}

export default App;
